// import styles from './index.less'
import _ from '@/i18n'
import type { TokenDataType, TokenStateType } from '@/models/dataType'
import { Input } from 'antd'
import { connect } from 'umi'
import { SearchOutlined } from '@ant-design/icons'

const escapeRegExpWildcards = (searchStr: string) => {
  const regExp = /([\(\[\{\\\^\$\}\]\)\?\*\+\.])/gim
  if (searchStr && regExp.test(searchStr)) {
    return searchStr.replace(regExp, '\\$1')
  }
  return searchStr
}

const searchByKeywords = (keywords: string, searchArr) => {
  const keywordsExp = new RegExp('.*?' + escapeRegExpWildcards(keywords) + '.*?', 'img')

  return searchArr.filter((v: TokenDataType) => {
    return keywordsExp.test(v.symbol) || keywords == v.tokenId || keywords == v.liquidity
  })
}

function SearchInput(props) {
  const { changeShowList, tokenDatasSorted,_attrTokens=[] } = props
  let list = _attrTokens&&_attrTokens.length>0?_attrTokens: tokenDatasSorted
  // TODO 这地方逻辑有问题，几种情况 之后需要修改，需要状态提升，因为在刷新的时候 页面也需要重新过滤

  const handleChange = (e) => {
    const { value } = e.target

    if (!value) {
      return changeShowList(list)
    }
    const searchResultList = searchByKeywords(value, list)
    changeShowList(searchResultList)
  }



  return (
    <Input
      // size="large"
      // className={styles.search_input}
      placeholder={_('search_tips')}
      onChange={handleChange}
      className="search_input"
      allowClear={true}
      prefix={<SearchOutlined />}
    />
  )
}

const mapStateToProps = ({ tokens }: { tokens: TokenStateType }) => {
  return {
    ...tokens,
  }
}

export default connect(mapStateToProps)(SearchInput)
