// import styles from './index.less'
import type { WalletStateType } from '@/models/dataType'
import { Input } from 'antd'
import { connect } from 'umi'
import _ from '@/i18n'

function SearchInputRemote(props) {
  const { changeShowList, dataSourceChainId, currentWallet } = props

  const handleChange = async (e) => {
    if (!currentWallet) return
    const { value } = e.target

    const data = await currentWallet.searchNewToken({
      chainId: dataSourceChainId,
      value: value.trim(),
    })

    if (data && data.length > 0) changeShowList(data)
  }

  return (
    <Input
      size="large"
      // className={styles.search_input}
      placeholder={_('search_tips')}
      onChange={handleChange}
      className="search_input"
    />
  )
}

const mapStateToProps = ({ wallet }: { wallet: WalletStateType }) => {
  return {
    ...wallet,
  }
}

export default connect(mapStateToProps)(SearchInputRemote)
