import styles from './index.less'
import _ from '@/i18n'
import SuccessIcon from '@/assets/svg/success.svg'
import { strAbbreviation } from '@/common/utils'
import Token from '@/components/chooseToken/token'
import { DropDownArr } from '@/components/ui'
import { Modal } from 'antd'
import Clipboard from '@/components/clipboard'
import CopySvg from '@/assets/svg/copy.svg'
import { getChainById } from '@/layout/header/chainData'

function Result(props) {
  const { visible, close, txid, tokenIn, tokenOuts, title, amountTitle, receiveTitle, chainId } =
    props
  let tokenIns = props.tokenIns || [tokenIn]
  const chainData = getChainById(chainId)
  return (
    <Modal
      width={420}
      footer={null}
      open={visible}
      getContainer={() => document.getElementById('J_Page') || document.body}
      onCancel={close}
      closable={false}
      wrapClassName={styles.modal}
    >
      <div className={`${styles.container} ${styles.result}`}>
        <div className={styles.result_icon}>
          <img src={SuccessIcon} style={{ width: 60, height: 60 }} />
        </div>
        <div className={styles.result_title}>{title}</div>
        <div className={styles.result_link}>
          <a
            className="primary"
            href={`${chainData.exporerTxUrl}/${txid}?network=${chainData.network}`}
            target="_blank"
            rel="noreferrer"
          >
            {_('browse')}: {strAbbreviation(txid, [16, 16])}
          </a>
          <Clipboard text={txid}>
            <img src={CopySvg} style={{ cursor: 'pointer' }} />
          </Clipboard>
        </div>

        <div className={styles.card}>
          <div className={styles.label}>{amountTitle || _('paid')}</div>
          {tokenIns.map((item) => {
            // console.log('item', item)
            if (item.amount && item.tokenId)
              return (
                <div className={styles.value} key={item.tokenId || item.icon}>
                  <div className={styles.amount}>{item.amount}</div>
                  <div className={styles.token}>
                    <Token data={item} />
                  </div>
                </div>
              )
          })}
        </div>
        {tokenOuts && tokenOuts.length > 0 && (
          <>
            <div className={styles.arr}>
              <DropDownArr />
            </div>

            <div className={styles.card}>
              <div className={styles.label}>{receiveTitle || _('received')}</div>
              {tokenOuts.map((item) => {
                if (item.amount)
                  return (
                    <div className={styles.value} key={item.tokenId || item.icon}>
                      <div className={styles.amount}>{item.amount}</div>
                      <div className={styles.token}>
                        <Token data={item} />
                      </div>
                    </div>
                  )
              })}
            </div>
          </>
        )}
      </div>
    </Modal>
  )
}

export default Result
