import styles from './index.less'
import _ from '@/i18n'
import Icon from '@/components/icon'

export default function PanelMenu(props) {
  const { menuIndex, setMenuIndex, menuDatas, className } = props
  return (
    <div className={`${styles.panel_menu} ${className}`}>
      {menuDatas.map((menu, index) => {
        const { icon, key, label, disabled, hightlightIcon } = menu
        if (menuIndex === index) {
          return (
            <div className={`${styles.item} ${styles.selected_item} item${index}`} key={key}>
              <div className={`${styles.item_inner} primary`}>
                {icon &&
                  (hightlightIcon ? (
                    <img src={hightlightIcon} className={styles.icon} />
                  ) : (
                    <Icon type={icon} />
                  ))}{' '}
                {_(label)}
              </div>
            </div>
          )
        }
        return (
          <div
            className={styles.item}
            key={key}
            onClick={disabled ? undefined : () => setMenuIndex(index)}
          >
            <div className={styles.item_inner}>
              {icon && <Icon type={icon} />} {_(label)}
            </div>
          </div>
        )
      })}
    </div>
  )
}
