import { DownloadArr } from '../ui'
import styles from './index.less'
import DefultIcon from '../icon/defult'

export type TokenType = {
  icon: string
  symbol: string
  tokenId: string
}

function Token(props: { data: TokenType; changeToken?: () => void; className?: string }) {
  const { data, changeToken, className } = props
  const { icon, symbol, tokenId } = data || {}

  return (
    <div
      className={`${styles.token} ${className}`}
      onClick={changeToken}
      style={{ cursor: changeToken ? 'pointer' : 'default' }}
    >
      {icon ? <img src={icon} /> : <DefultIcon tokenId={tokenId} size={25} />}
      <div className={styles.name}>{symbol}</div>
      {changeToken && <DownloadArr />}
    </div>
  )
}
export default Token
