import { Slider } from 'antd'
import styles from './index.less'
import BN from 'bignumber.js'
import { toFixed } from '@/common/utils'

type propsType = {
  amount: number
  max: number
  decimal: number
  onChange: (value: number | string) => void
}

export default function CustomSlider(props: propsType) {
  const { amount, onChange, max, decimal } = props
  const formatter = (value: number | undefined) => {
    const v = new BN(value || 0).div(max).multipliedBy(100).toString()
    return `${toFixed(v, 2)}%`
  }
  return (
    <div className={styles.slider}>
      <Slider
        value={amount}
        onChange={(value) => {
          const v = value === max ? value : toFixed(value, 4)
          onChange(v)
        }}
        max={max}
        step={decimal || 0.0001}
        tooltip={{
          placement: 'bottom',
          formatter,
          getPopupContainer: () => document.getElementById('J_repay') || document.body,
        }}
      />
    </div>
  )
}
