import { Modal } from 'antd'
import { useEffect, useState } from 'react'
import { DownloadArr } from '../ui'
import styles from './index.less'
import _ from '@/i18n'
import Token from './token'
import USD from '@/components/price/usd'
import SearchInput from '../search'
import SearchInputRemote from '../search/remote'
import { connect } from 'umi'
import type { TokenDataType, TokenStateType, WalletStateType } from '@/models/dataType'
import DefultIcon from '../icon/defult'
import { Loading as Spin } from '@/components/loading'

function ChooseToken(props) {
  const {
    changeToken,
    tokenDatas,
    _attrsTokens=[],//直接通过attr传递过来的token 列表。如果该值为 空数组，则沿用之前逻辑
    tokenDatasSorted,
    osdTokenData,
    type = '',
    currentToken,
    tokenClassName,
    isLogin,
    loadingData,
  } = props
  const [modalVisible, setModalVisible] = useState(false)
  const isLp = type === 'lp'
  const [initList, setInitList] = useState([])
  const [list, setList] = useState(undefined)
  let currentTokenIndex: TokenDataType | undefined = undefined
  if (currentToken && currentToken.tokenId && tokenDatas)
    currentTokenIndex = tokenDatas.findIndex((v) => v.tokenId === currentToken.tokenId)

  useEffect(() => {
    let _list = tokenDatasSorted || tokenDatas || [];
    if(_attrsTokens.length>0){
      _list=_attrsTokens
    }
    const tokenDataWithOsd = _list.length > 0 ? [..._list, osdTokenData] : []
    let newList = tokenDataWithOsd
    if (isLp || type === 'addLiq' || type === 'colls') {
      newList = _list
    } else if (type === 'borrow') {
      newList = newList.filter((v) => parseFloat(v.userUpdatedAt) > 0)
    }
    setInitList(newList)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [(tokenDatas || []).length, tokenDatas, type, isLogin,_attrsTokens.length])

  const choose = (item: TokenDataType) => {
    setModalVisible(false)
    changeToken(item)
  }

  return (
    <>
      {typeof currentTokenIndex === 'undefined' ? (
        <div className={`${styles.token} ${styles.noToken}`} onClick={() => setModalVisible(true)}>
          <div className={styles.noData}>{_('select_asset')}</div>
          <DownloadArr />
        </div>
      ) : (
        <Token
          className={tokenClassName}
          changeToken={() => setModalVisible(true)}
          data={
            isLp
              ? {
                  icon: currentToken?.iconLp,
                  symbol: currentToken?.lpSymbol,
                  tokenId: currentToken?.liquidity,
                }
              : currentToken
          }
        />
      )}

      <Modal
        width={420}
        footer={null}
        open={modalVisible}
        getContainer={() => document.getElementById('J_Page') || document.body}
        onCancel={() => setModalVisible(false)}
        closable={false}
        wrapClassName={styles.modal}
      >
        <Spin spinning={(tokenDatas && tokenDatas.length < 1) || loadingData}>
          <div className={styles.container}>
            <div className={styles.search_wrap}>
              {type === 'newpool' ? (
                <SearchInputRemote changeShowList={(newlist) => setList(newlist)} />
              ) : (
                <SearchInput changeShowList={(newlist) => setList(newlist)} _attrTokens={initList} />
              )}
            </div>
            <div className={styles.content}>
              {(list || initList).map((item, index) => {
                const {
                  symbol,
                  fullName,
                  icon,
                  iconLp,
                  tokenId,
                  userLpBalance,
                  userTokenBalance,
                  lpSymbol,
                } = item
                const balance = type === 'lp' ? userLpBalance : userTokenBalance
                return (
                  <div
                    className={
                      currentTokenIndex === index
                        ? `${styles.item} ${styles[`no${index}`]} ${styles.selected_item}`
                        : `${styles.item} ${styles[`no${index}`]}`
                    }
                    key={tokenId}
                    onClick={() => choose(item)}
                  >
                    <div className={styles.info}>
                      <div className={styles.icon}>
                        {iconLp || icon ? (
                          <img src={isLp ? iconLp : icon} />
                        ) : (
                          <DefultIcon tokenId={tokenId} size={25} />
                        )}
                      </div>
                      <div className={styles.title}>
                        <div className={styles.symbol}>{isLp ? lpSymbol : symbol}</div>
                        <div className={styles.full_name}>{fullName}</div>
                      </div>
                    </div>
                    <div className={styles.balance}>
                      <div className={styles.no}>{balance}</div>
                      <div className={styles.usd}>
                        <USD data={{ amount: Number(balance), token: item }} />
                      </div>
                    </div>
                  </div>
                )
              })}
            </div>
          </div>
        </Spin>
      </Modal>
    </>
  )
}

const mapStateToProps = ({
  tokens,
  wallet,
  loading,
}: {
  tokens: TokenStateType
  wallet: WalletStateType
  loading: any
}) => {
  const effects = loading.effects
  return {
    ...tokens,
    ...wallet,
    loadingData: effects['tokens/getData'] || false,
  }
}

export default connect(mapStateToProps)(ChooseToken)
