import type { TokenDataType, WalletStateType } from '@/models/dataType'
import { connect } from 'umi'
import styles from './index.less'
import _ from '@/i18n'
import { toFixed } from '@/common/utils'

export function TokenBalance(props: {
  token: TokenDataType
  hiddenLabel?: boolean
  setFullAmount?: (balance: number) => any
  type?: string
}) {
  const { token = {} as TokenDataType, hiddenLabel, setFullAmount, type } = props

  const balance = type === 'lp' ? token.userLpBalance : token.userTokenBalance
  return (
    <div>
      {hiddenLabel ? '' : `${_('balance')}: `}
      <span
        className={styles.balance}
        onClick={setFullAmount ? () => setFullAmount(balance || '0') : undefined}
      >
        {balance ? toFixed(balance, 4) : 0}
      </span>
    </div>
  )
}

const mapStateToProps = ({ wallet }: { wallet: WalletStateType }) => {
  return {
    ...wallet,
  }
}

export default connect(mapStateToProps)(TokenBalance)
