import { useRef } from 'react'
import ChooseToken from '../chooseToken'
import styles from './index.less'
import type { TokenDataType } from '@/models/dataType'
import type { InputRef } from 'antd'
import { contains } from '@/common/utils'
import { TokenBalance } from '../balance/token'
import USD from '@/components/price/usd'
import { InputNumber } from 'antd'
import { AmountLoading } from '@/common/config'

function SimpleCard(props: {
  amount: string
  onAmountChange?: (amount: number) => any
  onChangeToken?: (item: TokenDataType) => any
  [key: string]: any
}) {
  const {
    onAmountChange,
    amount,
    token,
    onChangeToken,
    left,
    right,
    title,
    rightBottom,
    type = '',
    className,
    slider,
    priceType,
  } = props
  const inputRef = useRef<InputRef>(null)

  const changeValue = (value: number) => {
    if (onAmountChange) onAmountChange?.(value)
  }

  let amountNode
  if (amount === AmountLoading) {
    amountNode = AmountLoading
  } else {
    amountNode = !amount ? (
      (amountNode = '$0')
    ) : (
      <USD data={{ amount, token }} priceType={priceType} />
    )
  }
  return (
    <div
      className={`${styles.card} ${className}`}
      onClick={(e) => {
        if (contains(e.currentTarget, e.target)) {
          inputRef.current!.focus()
        }
      }}
    >
      <div className={styles.label}>
        {left || (
          <div>
            {title}: {amountNode}
          </div>
        )}{' '}
        {right || (
          <TokenBalance
            token={token}
            setFullAmount={(value: number) => {
              if (onAmountChange) onAmountChange(value.toString())
            }}
            type={type}
          />
        )}
      </div>
      <div className={styles.value}>
        <InputNumber
          className="inputTxt"
          value={amount}
          onChange={changeValue}
          ref={inputRef}
          min={0}
          placeholder="0.00"
        />
        {rightBottom || (
          <ChooseToken currentToken={token} changeToken={onChangeToken} type={type} _attrsTokens={props.tokens||[]} />
        )}
      </div>
      {slider}
    </div>
  )
}
export default SimpleCard
