import { usdPrice } from '@/common/utils'

function USD(props) {
  // console.log(props.data, props.data instanceof Array)
  let { data, className, priceType } = props
  if (!(data instanceof Array)) {
    data = [data]
  }
  const usd = usdPrice(data, priceType)

  return <span className={className}>${usd}</span>
}

export default USD
